/* @import-normalize; */
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap');

* {
  box-sizing: border-box;
}

:root {
  /* Typography */
  --family: 'Nunito Sans', sans-serif;
  --fs-sm: 14px;
  --fs-md: 16px;
  --fw-light: 300;
  --fw-normal: 600;
  --fw-bold: 800;

  /* Other */
  --radii: 0.5rem;
}

body[data-theme='dark'] {
  --colors-text: hsl(0, 0%, 100%);
  --colors-bg: hsl(207, 26%, 17%);
  --colors-ui-base: hsl(209, 23%, 22%);

  --shadow: rgba(245, 245, 245, 0.2) 0 0 8px;
}
body[data-theme='light'] {
  --colors-text: hsl(200, 15%, 8%);
  --colors-bg: hsl(0, 0%, 98%);
  --colors-ui-base: hsl(0, 0%, 100%);

  --shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

body {
  height: 100%;
  margin: 0;
  font-family: var(--family);
  color: var(--colors-text);
  font-weight: var(--fw-light);
  background-color: var(--colors-bg);
}



main {
  /* height: calc(100% - 112.72px - 72px); */
  min-height: 100%;
}




.pagination{
  background-color: #fafafa;
  width: 363px;
  margin: 0 auto;
  padding: 10px 0;
}




.header_img {
  width: 120px;
}


ul, ol {
  list-style-type: none;
  padding: 0;
}
.weather {
  margin: 15px 0;
}
.weather_list li{
  padding: 7px;
}


* {
  margin: 0;
  padding: 0;
}
footer {
  min-height: calc(2vh);
}
.footer_github {
  display: flex;
  align-items: center;


  text-decoration: none;
  color: #fff;
}

.footer_github img {
  width: 40px;
  height: 40px;
}